.swiper,
.swiper-wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0; }

.swiper {
  transition: max-height 0.25s ease-in-out;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 10px;
  background-color: #f8f8f8; }
  .swiper-pagination {
    display: none;
    transform: translateY(10px); }
  .swiper-pagination-bullet {
    opacity: 1;
    background-color: #dddddd; }
  .swiper-pagination-bullet-active {
    background-color: #b5b6bc;
    transform: scale(1.3); }
  .swiper::after {
    content: '';
    position: absolute !important;
    z-index: 2;
    height: 71%;
    width: 30px;
    background-image: linear-gradient(to right, transparent 0%, white 100%);
    top: 0;
    right: 0;
    display: none; }

@media (max-width: 768px) {
  .swiper-pagination {
    display: block; }
  .swiper {
    padding-bottom: 30px;
    display: block; }
  .swiper::after {
    display: block; } }
