@charset "UTF-8";
.service {
  padding: 16px;
  max-width: 1440px;
  margin: 0 auto; }
  .service__wrapper {
    display: block; }
  .service__row {
    box-sizing: border-box;
    height: 64px;
    display: grid;
    grid-template-columns: minmax(210px, 1fr) 1fr minmax(100px, 1fr) 150px;
    align-content: center;
    align-items: center;
    padding: 20px;
    background-color: white;
    position: relative;
    margin: 0 auto;
    gap: 20px; }
    .service__row:nth-child(2) {
      border-radius: 6px 6px 0 0; }
    .service__row:last-child {
      border-radius: 0 0 6px 6px; }
  .service__row:not(:last-child):not(:first-child)::after {
    content: '';
    width: 100%;
    bottom: 0;
    height: 1px;
    position: absolute;
    background-image: linear-gradient(to right, transparent 3%, #eaeaea 3%, #eaeaea 97%, transparent 97%); }
  .service__header {
    background-color: transparent;
    color: #7e7e82; }
  .service__header * {
    margin: 0;
    padding: 0; }
  .service__name::before, .service__price::before, .service__time::before {
    display: block;
    color: #7e7e7e;
    margin-top: 10px; }
  .service__order-button {
    background-image: url(./static/ArrowRight.svg);
    background-position: 90% 50%;
    width: 136px;
    height: 32px;
    text-transform: uppercase;
    display: block;
    margin-left: auto; }

@media (max-width: 768px) {
  .service {
    position: relative; }
    .service__name::before {
      content: 'Ремонтные услуги'; }
    .service__price::before {
      content: 'Цена'; }
    .service__time::before {
      content: 'Срок'; }
    .service__row {
      max-width: 295px;
      background-image: none;
      border-radius: 6px; }
    .service__row {
      display: block;
      height: 200px; }
    .service__header {
      display: none; }
    .service__order-button {
      position: absolute;
      bottom: 16px;
      right: 16px; } }
