.devices {
  margin: 0 auto;
  max-width: 1120px; }

.devices__wrapper {
  display: grid;
  grid-template-columns: repeat(4, minmax(220px, 1fr));
  padding: 12px 0;
  margin: 0 auto;
  max-width: 1120px;
  gap: 16px 24px;
  overflow: hidden;
  transition: max-height 0.25s ease-in-out; }

.devices__slide {
  box-sizing: border-box;
  height: 160px;
  max-width: 295px; }

.devices__button {
  background-color: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  padding: 16px;
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-start;
  transition: transform 0.1s ease-out, box-shadow 0.2s ease-out, background-position 0.1s ease-in-out;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px; }
  .devices__button::after {
    content: '';
    position: absolute;
    background: url(./static/Go.svg) no-repeat 50% 50%;
    width: 40px;
    height: 40px;
    right: 0;
    bottom: 0;
    padding: 16px; }
  .devices__button:hover {
    border: 1px solid #ff3e79; }
  .devices__button:active {
    transform: scale(0.95);
    box-shadow: 0px 0px 12px 0px rgba(34, 60, 80, 0.3); }

@media (min-width: 769px) and (max-width: 1440px) {
  .devices__wrapper {
    grid-template-columns: repeat(3, minmax(220px, 1fr)); } }
