.aside {
  position: fixed;
  top: 0;
  flex-direction: column;
  min-width: 320px;
  width: 320px;
  transform: translateX(-100%);
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
  height: 100%;
  background-color: #ffffff;
  z-index: 3; }
  .aside__logo-link {
    display: block;
    width: 98px;
    height: 52px;
    padding-left: 20px; }
  .aside__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px; }
  .aside-list {
    margin-top: 30px; }
  .aside__footer {
    margin-top: 20%;
    margin-bottom: 32px;
    margin-left: 24px; }
  .aside__button--icon--close {
    margin-left: 24px; }
  .aside__button--icon--search {
    margin-right: 24px;
    margin-left: auto; }
  .aside__list-button {
    font-family: inherit;
    font-weight: 500;
    font-size: 16px;
    text-align: start;
    background: transparent;
    border: none;
    color: #1b1c21;
    width: 100%;
    position: relative;
    padding: 14px 24px; }
    .aside__list-button:hover {
      color: #7e7e82;
      cursor: pointer; }
      .aside__list-button:hover::before {
        content: '';
        position: absolute;
        background-color: #41f6d7;
        border-radius: 16px;
        width: 6px;
        height: 38px;
        top: 8px;
        margin-left: -2px;
        left: 0; }
  .aside__email {
    display: block;
    font-family: inherit;
    font-weight: 500;
    color: #1b1c21;
    text-decoration: none;
    margin-top: 30px; }
  .aside__phone-number {
    display: block;
    font-family: inherit;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -0.6px;
    text-align: left;
    color: #1b1c21;
    text-decoration: none;
    margin-top: 20px;
    margin-bottom: 10px; }

.aside.modalOpen {
  transform: translateX(0); }

@media (min-width: 1440px) {
  .aside {
    display: block;
    transform: translateX(0);
    box-shadow: none; } }
