@charset "UTF-8";
@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal; }

body {
  min-width: 330px;
  max-width: 1440px;
  font-family: 'TT Lakes';
  background-color: #f8f8f8;
  margin: 0 auto; }

.main {
  max-width: 1440px;
  padding: 0;
  margin: 0 auto;
  transition: margin-left 0.25s ease-in-out; }

.button-icon {
  -webkit-user-drag: none; }

.round-button {
  font-family: inherit;
  font-weight: 700;
  font-size: 12px;
  color: white;
  width: 40px;
  height: 40px;
  border: 0;
  border-radius: 2rem;
  background-color: #ff3e79;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: transform 50ms ease-in-out, box-shadow 0.1s ease-in-out; }
  .round-button:hover {
    cursor: pointer;
    transform: scale(1.05);
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.4); }
  .round-button:active {
    transform: scale(0.95); }

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0; }

.flex {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  flex-direction: row;
  -moz-column-gap: 16px;
       column-gap: 16px; }

.header {
  justify-content: space-between;
  background-color: #fff;
  padding: 16px; }
  .header__logo::before, .header__logo::after {
    content: '';
    position: absolute;
    display: inline-block;
    top: 50%;
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    margin-top: -16px; }
  .header__logo::before {
    left: 0;
    display: none; }
  .header__logo::after {
    right: 0; }

.logo {
  width: 98px;
  height: 52px;
  position: relative;
  padding: 0 16px;
  background-image: url(./static/Logo.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%; }

.header__buttons--tablet {
  position: relative;
  padding-right: 20px;
  display: none; }
  .header__buttons--tablet::after {
    content: '';
    position: absolute;
    display: inline-block;
    top: 50%;
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    right: 0;
    margin-top: -16px; }

.nav-header__buttons {
  display: none; }

.nav-header {
  justify-content: space-between;
  margin-right: 32px; }

.nav-header__title {
  font-weight: 700;
  font-size: 27px;
  padding: 8px 0;
  padding-left: 30px;
  position: relative; }
  .nav-header__title::before {
    content: '';
    background-image: url(./static/highlight.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 4px;
    height: 40px;
    position: absolute;
    left: 0; }

.nav__list {
  display: flex;
  overflow-x: scroll;
  padding: 6px 14px;
  margin-bottom: 32px;
  flex-wrap: nowrap; }

.nav__link {
  background-color: transparent;
  color: #7e7e82;
  border: none;
  white-space: nowrap;
  height: 40px;
  font-family: inherit;
  font-size: 16px;
  border: solid 2px rgba(0, 0, 0, 0);
  border-radius: 5px;
  transition: border 0.2s, background-color 0.2s;
  line-height: 40px;
  padding: 6px 12px;
  margin: 0 2px; }

.nav__link--active,
.nav__link:hover {
  background-color: #ffffff;
  border: 2px solid #b8ffec;
  cursor: pointer;
  text-decoration: underline; }

.main__subtitle {
  font-size: 16px;
  text-transform: uppercase;
  color: #7e7e7e;
  background-image: linear-gradient(to right, #f8f8f8, white);
  height: 56px;
  margin: 16px auto;
  padding: 0 16px;
  align-content: center;
  border-top: 1px solid #d9fff5; }

.main__content {
  align-items: flex-start; }

.main__text {
  margin: 16px;
  overflow: hidden;
  transition: max-height 0.25s ease-in-out; }

.main__text-wrapper {
  width: 50%;
  position: relative; }

.main__image {
  width: 50%;
  margin-top: 16px; }

.aside-bg,
.modal-bg {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: white;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  display: none;
  -webkit-user-drag: none; }

.aside-bg {
  z-index: 2; }

.modal-bg {
  z-index: 4; }

.bg-open {
  opacity: 0.9 !important; }

.lang-buttons {
  gap: 2px; }

.lang-button {
  font-family: inherit;
  font-size: 16px;
  border: 1px solid transparent;
  padding: 2px;
  border-radius: 4px;
  background-color: transparent;
  transition: color 0.2s, border 0.2s; }
  .lang-button:hover {
    color: #7e7e82;
    border: 1px solid #7e7e82;
    cursor: pointer; }

.nav-header__button--icon--repair,
.nav-header__button--icon--status {
  margin-left: 140px;
  position: relative;
  text-wrap: nowrap;
  color: black;
  font-weight: 500; }

.nav-header__button--icon--status::before,
.nav-header__button--icon--repair::before {
  position: absolute;
  right: 25px;
  padding: 8px 20px;
  font-size: 16px; }

.nav-header__button--icon--repair::before {
  content: 'Оставить заявку'; }

.nav-header__button--icon--status::before {
  content: 'Статус ремонта'; }

.expand-button {
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 5px;
  margin-left: 16px;
  padding: 5px;
  transition: transform 0.2s, border 0.2s;
  display: flex;
  align-items: center; }
  .expand-button:hover {
    cursor: pointer; }
  .expand-button:active {
    border: 1px solid rgba(0, 0, 0, 0.3);
    font-weight: 900;
    transform: scale(0.95); }

.expand-button__icon {
  margin-right: 16px;
  height: 14px;
  transition: transform 0.3s linear; }

.expand-button__text {
  font-family: 'TT Lakes';
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.4px; }

@media (max-width: 768px) {
  .main__content {
    display: block; }
  .main__image {
    border-radius: 0;
    width: 100%; }
  .main__text-wrapper {
    width: 100%; }
  .aside {
    transform: translateX(-100%); }
  .expand-button:not(.main__expand-button) {
    display: none; } }

@media (min-width: 769px) and (max-width: 1440px) {
  .main__content {
    display: flex; }
  .main__image {
    border-radius: 10px;
    margin-right: 25px; }
  .header__logo::before {
    display: inline-block; }
  .header__logo::after {
    display: none; }
  .header__buttons--tablet {
    display: flex; }
  .aside {
    transform: translateX(-100%); } }

@media (min-width: 1440px) {
  .main {
    margin-left: 320px; }
  .header__group-right,
  .header__group-left,
  .aside__button--icon--close {
    display: none; }
  .header {
    padding: 0; }
  .nav__list {
    overflow-x: visible;
    flex-wrap: wrap; }
  .main__image {
    border-radius: 10px;
    margin-right: 30px; }
  .nav-header__buttons {
    display: flex; } }
